<template>
	<div class="con">
		<p>标题</p>
		<label>
			<input type="text" v-model="title">
		</label>
		<label>
			<p>内容</p>
			<input type="text" v-model="content">
		</label>
		<button class="btn" type="button" @click="add()">添加</button>
	</div>
</template>

<script>
	import store from '@/store'
	export default{
		name:'add',
		store,
		data() {
			return {
				title:'',
				content:''
			}
		},
		methods:{
			add(){
				store.commit('addItem',{
					title:this.title,
					content:this.content
				})
				this.title=''
				this.content=''
				this.$router.push('/home/list')
			}
		}
	}
	
</script>

<style lang="scss">
	.con{
		display: block;
		text-align: center;
	}
	.btn{
		background: #42b983;
		color: #fff;
		border: none;
		font-size: 16px;
		padding: 3px;
		margin: 10px auto 0;
	}
</style>